import { useQuery, useQueryClient } from "@tanstack/react-query";
import { axiosGet, axiosPut } from "@/services/axiosService";
import useStore from "@/pages/omni-builder/store";
import axios from "axios";
import environment from "@/constants/Environment";

export default function useWhatsAppConfig() {
  const queryClient = useQueryClient();
  const setIsWhatsappLoading = useStore((state) => state.setIsWhatsappLoading);

  return useQuery({
    queryKey: ["configurations", "whatsapp"],
    queryFn: async () => {
      const cachedData: any =
        queryClient.getQueryData(["configurations", "whatsapp"]) ?? null;

      if (!cachedData) {
        setIsWhatsappLoading(true);
      }
      const response = await axiosGet("/users/getAppConfig?siteName=whatsapp");
      let result: any = null;

      if (response?.data) {
        result = await fetchConfig(response.data, cachedData);
      }

      // result contains accessToken, profile, phoneNumbers & wabaIds
      setIsWhatsappLoading(false);
      return result;
    },
    staleTime: Infinity,
  });
}

async function fetchConfig(config: any, cachedData: any) {
  try {
    let result: any = {
      accessToken: config.accessToken,
      name: config.accountName,
      picture: config.picture,
      connections: config.connections,
      phoneNumbers: cachedData?.phoneNumbers ?? [],
      wabaIds: cachedData?.wabaIds ?? [],
    };

    if (!cachedData) {
      const { phoneNumbers, wabaIds } = await fetchPhonesAndWabaIds(
        config?.accessToken
      );
      result = {
        ...result,
        phoneNumbers,
        wabaIds,
      };
    }

    return result;
  } catch (error: any) {
    await axiosPut("/users/deleteAppConfig?siteName=whatsapp");
    if (error.cause === "suppress") return;
    const errMsg = typeof error === "string" ? error : error.message;
    throw new Error(errMsg);
  }
}

async function fetchPhonesAndWabaIds(accessToken: string) {
  const environmentData = environment();

  const wabaDetails = await axios.get(
    `https://graph.facebook.com/${environmentData.META_API_VERSION}/debug_token?input_token=${accessToken}`,
    {
      headers: {
        Authorization: `Bearer ${accessToken}`,
      },
    }
  );

  // a wabaId is whatsapp business id, basically if user has multiple businesses
  const wabaIds = wabaDetails?.data?.data?.granular_scopes.find(
    (e: any) => e.scope === "whatsapp_business_management"
  ).target_ids;

  let phoneNumbers = (
    await Promise.all(
      wabaIds.map(async (wabaId: any) => {
        const res = await axios.get(
          `https://graph.facebook.com/${environmentData.META_API_VERSION}/${wabaId}/phone_numbers`,
          {
            headers: {
              Authorization: `Bearer ${accessToken}`,
            },
          }
        );
        return res.data.data;
      })
    )
  ).flat();

  return { phoneNumbers, wabaIds };
}

