import { useState, useMemo, Fragment } from "react";
import useStore from "../store";
import type { Variable, Variables } from "../types";
import { VARIABLE_CATEGORY } from "../constant";
import { handleSearch } from "@/utils/handleSearch";
import { SearchIconSvg } from "@/assets/AiBotAssets";

interface Props {
  onSelect: (variable: Variable) => void;
  categoryFilter?: (category: string) => boolean;
  nodeFilter?: (type: string) => boolean;
  searchable?: boolean;
  customVars?: Variables;
}

export default function VariableDropdown({
  onSelect,
  categoryFilter,
  nodeFilter,
  searchable,
  customVars,
}: Props) {
  const _variables = useStore((state) => state.variables);
  const variables = customVars ?? _variables;
  const [searchText, setSearchText] = useState("");

  const { filteredVars, totalVarCount } = useMemo(() => {
    const filteredVars: Variables = {};
    let totalVarCount: number = 0;

    for (const category of VARIABLE_CATEGORY) {
      if (categoryFilter && !categoryFilter(category)) continue;
      for (const variable of variables[category] ?? []) {
        if (nodeFilter && !nodeFilter(variable.originNode.type)) continue;
        if (searchable && !handleSearch(searchText, variable.name)) continue;

        if (!filteredVars[category]) filteredVars[category] = [];
        filteredVars[category]!.push(variable);
        totalVarCount++;
      }
    }

    return { filteredVars, totalVarCount };
  }, [variables, searchText]);

  return (
    <div className="dropdown-items">
      {searchable && (
        <div className="whstp-search-input fx al-cnt p-relative">
          <SearchIconSvg />
          <input
            type="text"
            placeholder="Search.."
            value={searchText}
            onChange={(event) => setSearchText(event.target.value)}
            onClick={(event) => event.stopPropagation()}
            autoFocus
          />
        </div>
      )}
      <ul className="grey-scrollbar">
        {totalVarCount ? (
          VARIABLE_CATEGORY.map(
            (category, index) =>
              filteredVars[category]?.length && (
                <Fragment key={index}>
                  <span>{category} variable</span>
                  {filteredVars[category]!.map((variable) => (
                    <li key={variable.id} onClick={() => onSelect(variable)}>
                      {variable.name}
                    </li>
                  ))}
                </Fragment>
              )
          )
        ) : (
          <span>No variable Found</span>
        )}
      </ul>
    </div>
  );
}
