import type { Variable } from "../types";

export default function placeVariable(
  ref: HTMLTextAreaElement | HTMLInputElement,
  variable: Variable
) {
  const { value, selectionStart, selectionEnd } = ref;
  const preText = value.slice(0, selectionStart ?? value.length);
  const postText = value.slice(selectionEnd ?? value.length);
  const result = preText + `{ ${variable.name} }` + postText;

  return result;
}
